import React, {useCallback, useContext, useState} from "react";
import styled from "styled-components";
import {useAllowance, useApprove} from "../../../utils/token_server";
import {useModel} from "../../../hook/use_model";
import {SendModal, useSendModal} from "../../../widget/modal/send_modal";
import {Contract} from "web3-eth-contract";
import {ButtonStyle, ButtonWidget} from "../../../widget/base/button";
import {useStakeContext} from "../../../provider/stake_provider";
import {useWeb3} from "../../../provider/web3_provider";
import {useDeposit} from "../../../server/chef";
import {getBalanceNumber, getMaxNumber} from "../../../utils/formatBalance";
import BigNumber from "bignumber.js";
import {Grid, TextField} from "@material-ui/core";
import {TransactionModal} from "../../../widget/modal/transaction_modal";
import {HttpStatus, useWait} from "../../../utils/wait_fn";
import {WidthStyle} from "../../../widget/style/width_style";
import {FlexStyle, InputStyle, ButtonStyle as InputButton, InputWidget} from "../../../widget/base/input";
import {ModalContext} from "../../../provider/model_provider";

let value = '0';
export const StakeButtonWidget: React.FC<{
    handleWithdraw: (amount: string) => Promise<any>,
    handleDeposit: (amount: string) => Promise<any>,
    amount: BigNumber,
    maxNumber: BigNumber,
}> = ({handleWithdraw, handleDeposit, amount, maxNumber}) => {
    const {contract, lpAddr} = useStakeContext();
    const {contrcts} = useWeb3();

    const {chefAddress} = contrcts;

    const allowance = useAllowance(contract, chefAddress); // 检查权限
    const [approve] = useApprove(contract, lpAddr, chefAddress); // 授权方法

    const [handleApprove] = useModel(<SendModal confirmationFn={approve}/>); // 申请授权

    const {onDismiss, onPresent} = useContext(ModalContext);

    const handlePresent = useCallback((ev?: any) => {
        onPresent(<StakeWidget text={'Withdraw'} amount={amount} confirmationFn={handleWithdraw}/>)
    }, [onPresent]);
    const [withdraw] = useModel(<StakeWidget text={'Withdraw'} amount={amount} confirmationFn={handleWithdraw}/>);
    const [deposit] = useModel(<StakeWidget amount={maxNumber} text={'Deposit'} confirmationFn={handleDeposit}/>);

    return (
        <div>
            {allowance <= 0 ?
                <ButtonStyle isDisable={false} onClick={handleApprove}>Approve</ButtonStyle> :
                <Grid container spacing={4}>
                    <Grid item sm={6} md={6}>
                        <ButtonStyle isDisable={amount.eq(0)} onClick={() => {
                            if (!amount.eq(0)) {
                                withdraw();
                            }

                        }}>Withdraw</ButtonStyle>
                    </Grid>
                    <Grid item sm={6} md={6}>
                        <ButtonStyle isDisable={false} onClick={deposit}>Deposit</ButtonStyle>
                    </Grid>
                    {/*<Grid item sm={6} md={6}>*/}
                    {/*    <ButtonStyle isDisable={false} onClick={handlePresent}>Depossit</ButtonStyle>*/}
                    {/*</Grid>*/}
                </Grid>
            }
        </div>
    );
}

const StakeWidget: React.FC<{
    confirmationFn: (ev: string) => Promise<any>,
    text: string,
    amount: BigNumber
}> = ({
          confirmationFn,
          text,
          amount
      }) => {
    value = '0';
    const {request, status, data,setStatus} = useWait(confirmationFn);
    const {onDismiss,} = useContext(ModalContext);


    return (
        <TransactionModal status={status} txHash={data}>
            <StakeStyle>
                <h2>Amount to {text}  <div className={'balance'}>Balance: <span>{getBalanceNumber(amount)}</span></div></h2>
                <FlexStyle>
                    <InputStyle>
                        <TextField

                            // onInput={(ev) => {
                            //     // value = ev.target.value;
                            //     console.log('12');
                            //     setNumber('22');
                            //     setStatus(HttpStatus.start);
                            // }}

                            onChange={(ev) => {
                                value = ev.target.value;
                                // setNumber(value);
                            }}
                        />

                    </InputStyle>

                    <InputButton onClick={() => {

                    }}>
                        Max
                    </InputButton>
                </FlexStyle>

                <div style={{margin: '30px 0'}}>
                    <ButtonStyle isDisable={false} onClick={() => request(value)}>{text}</ButtonStyle>
                </div>

                <BorderStyle onClick={onDismiss}>Cancel</BorderStyle>
            </StakeStyle>
        </TransactionModal>

    );
}

